
body {
	background: #f6f6f6;
}

.dgds_card {
	background: #fff;
    border: 1px solid #ddd;
	box-shadow: 0 1px 2px rgba(0,0,0,0.025);
	margin-bottom: 15px;
	padding: 15px;
}

.dgds_section-heading {
	border-bottom: 1px solid #e0e0e0;
	text-transform: uppercase;
	font-size: 16px;
	padding: 5px;
}
.bc_nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background: #ededed;
  display: inline-block; }
  .bc_nav-list li {
    border-left: 1px solid lightgray;
    display: inline-block;
    padding: 15px 30px;
    text-align: center; }
    .bc_nav-list li a {
      color: #616161;
      text-transform: uppercase;
      font-family: "Montserrat", sans-serif;
      font-weight: 700; }
    .bc_nav-list li i {
      color: lightgray;
      display: block;
      font-size: 22px;
      margin-bottom: 5px; }
    .bc_nav-list li:last-child {
      border-right: none; }

.bc_link-list {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .bc_link-list li {
    display: inline-block; }
    .bc_link-list li a {
      display: block; }

body {
  font-family: "Crimson Text", serif;
  font-size: 20px;
  font-weight: 400; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif; }

h1 {
  font-weight: 700; }

* {
  box-sizing: border-box; }

body {
  margin: auto;
  max-width: 1400px;
  width: 100%; }

/* This needs to be removed in favor of containers */
a {
  transition: all ease-in-out 0.3s; }

.dg_site-header {
  position: relative; }

.dg_brand {
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  position: absolute;
  text-align: center;
  text-transform: uppercase; }
  .dg_brand .dg_logo-container,
  .dg_brand .dg_logo {
    margin-bottom: 7.5px; }
  .dg_brand .dg_logo-container {
    background: #feb30c;
    font-size: 12px;
    font-weight: 900;
    padding: 7.5px 0 2.5px; }
  .dg_brand .dg_logo {
    max-width: 69px;
    width: 100%; }
  .dg_brand .dg_logo-subtext {
    font-size: 8px;
    letter-spacing: 6px; }

.dg_nav {
  margin: 0; }

.dg_primary-nav {
  background: #ededed;
  font-size: 16px;
  text-align: right; }
  .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item {
    padding: 0; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item a {
      border-bottom: 3px solid transparent;
      border-top: 3px solid transparent;
      display: block;
      padding: 12px 15px; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item i {
      transition: all ease-in-out 0.3s; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item:hover a {
      border-bottom-color: #feb30c; }
    .dg_primary-nav .dg_primary-nav-list .bc_nav-list-item:hover i {
      color: #feb30c; }

.dg_secondary-nav {
  background: #333;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 100;
  padding: 0 15px;
  text-align: right; }
  .dg_secondary-nav .dg_secondary-nav-list a {
    padding: 15px; }
    .dg_secondary-nav .dg_secondary-nav-list a:hover {
      background: #616161; }
  .dg_secondary-nav a {
    color: #fff; }

